import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Row, Col, Divider, Card } from 'antd';
import { pageLoadedLogin } from '../../../../application/actions/ui';
import FormLaporanGlobalProduksi from '../../../component/reportComponent/stock_global_produksi/form';
import TableLaporanStockGlobalProduksi from '../../../component/reportComponent/stock_global_produksi/tabel';
import BtnPrint from '../../../component/reportComponent/stock_global_produksi/btnprint';
import laporanStockSelector from '../../../../application/selectors/laporanstockglobalproduksi';
import ui from '../../../../application/selectors/ui';

const LaporanStockGolbalProduksi = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(pageLoadedLogin);
    document.title = 'Laporan Stock Golbal Produksi';
  }, [dispatch]);

  const datalapL = useSelector(laporanStockSelector.getAllLaporanStockGlobalProduksiL);
  const datalapR = useSelector(laporanStockSelector.getAllLaporanStockGlobalProduksiR);
  const typeTable = useSelector(ui.getTypeTableLayout);

  return (
    <Card title="Laporan Stock Global Produksi" bordered={false}>
      {typeTable === 'L' ? (
        <Row>
          <Col span={24}>
            <FormLaporanGlobalProduksi />
          </Col>
          <Col span={24} style={{ marginTop: 10, display: datalapL.length === 0 ? 'none' : '' }}>
            <Divider orientation="left">Tabel Laporan</Divider>
            <TableLaporanStockGlobalProduksi />
          </Col>
          <Col span={24} style={{ marginTop: 10, display: datalapL.length === 0 ? 'none' : '' }}>
            <BtnPrint />
          </Col>
        </Row>
      ) : (
        <Row>
          <Col span={24}>
            <FormLaporanGlobalProduksi />
          </Col>
          <Col span={24} style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}>
            <Divider orientation="left">Tabel Laporan</Divider>
            <TableLaporanStockGlobalProduksi />
          </Col>
          <Col span={24} style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}>
            <BtnPrint />
          </Col>
        </Row>
      )}
    </Card>
  );
};

export default LaporanStockGolbalProduksi;
