import { jsPDF } from 'jspdf';
import 'jspdf-autotable';

const pdfReport = (data = '') => {
  const doc = new jsPDF('l', 'mm', [297, 210]);
  let tableRows = [];
  let tableColumn = [];

  let finalY = 30;
  const tanggal = new Date(localStorage.getItem('tanggal_lap'));
  doc.text('Laporan Stock Global Produksi L', 14, 15);
  doc.setFontSize(20);
  doc.text('Nama Toko', 200, 15);

  doc.setFontSize(10);
  doc.setProperties({
    title: 'Bahan Tukang Detail',
  });
  doc.text(`PERIODE : ${tanggal.toLocaleDateString()}`, 14, 25);

  tableColumn = [
    [
      {
        content: `DIVISI`,
        rowSpan: 2,
      },
      {
        content: `AWH75`,
        colSpan: 4,
      },
      {
        content: `AYL70`,
        colSpan: 4,
      },
      {
        content: `AYL75`,
        colSpan: 4,
      },
      {
        content: `TOTAL`,
        rowSpan: 2,
      },
    ],
    [
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
    ],
  ];

  let total = 0;
  data.forEach((element) => {
    total =
      element.awh75_awal +
      element.awh75_in +
      element.awh75_out +
      element.awh75_sld +
      element.ayl70_awal +
      element.ayl70_in +
      element.ayl70_out +
      element.ayl70_sld +
      element.ayl75_awal +
      element.ayl75_in +
      element.ayl75_out +
      element.ayl75_sld;
    const row = [
      {
        content: element.divisi,
        styles: {
          halign: 'left',
        },
      },
      {
        content: element.awh75_awal.toFixed(3),
      },
      {
        content: element.awh75_in.toFixed(3),
      },
      {
        content: element.awh75_out.toFixed(3),
      },
      {
        content: element.awh75_sld.toFixed(3),
      },
      {
        content: element.ayl70_awal.toFixed(3),
      },
      {
        content: element.ayl70_in.toFixed(3),
      },
      {
        content: element.ayl70_out.toFixed(3),
      },
      {
        content: element.ayl70_sld.toFixed(3),
      },
      {
        content: element.ayl75_awal.toFixed(3),
      },
      {
        content: element.ayl75_in.toFixed(3),
      },
      {
        content: element.ayl75_out.toFixed(3),
      },
      {
        content: element.ayl75_sld.toFixed(3),
      },
      {
        content: total.toFixed(3),
      },
    ];
    tableRows.push(row);
  });

  const footer = [
    {
      content: '',
    },
    {
      content: data.reduce((a, b) => a + b.awh75_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.awh75_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.awh75_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.awh75_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl70_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl70_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl70_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl70_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl75_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl75_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl75_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.ayl75_sld, 0).toFixed(3),
    },
    {
      content: '',
    },
  ];
  tableRows.push(footer);

  const printed = [
    {
      content: `Printed By Admin`,
      colSpan: 12,
      styles: {
        fontStyle: 'italic',
        textColor: '#000',
        halign: 'left',
      },
    },
  ];
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: finalY,
    theme: 'plain',
    margin: { top: 10 },
    bodyStyles: {
      fontSize: 8,
      halign: 'right',
    },
    headStyles: {
      fontSize: 8,
      fillColor: '#E8E5E5',
      textColor: '#000',
      valign: 'middle',
      halign: 'center',
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  tableRows = [];
  tableColumn = [];
  finalY = doc.autoTableEndPosY() + 20;

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width;
  const pageHeight = doc.internal.pageSize.height;
  doc.setFontSize(10);
  for (let j = 1; j < pages + 1; j += 1) {
    const horizontalPos = pageWidth / 2;
    const verticalPos = pageHeight - 10;
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: 'center',
    });
  }
  const string = doc.output('datauristring');
  const x = window.open();
  x.document.open();
  x.document.write(
    `<html>
    <head>
    <title>Bahan Tukang Detail</title>
    </head>
    <body style='margin:0 !important'>
    <embed width='100%' height='100%'src='${string}'></embed>
    </body>
    </html>`,
  );
};

export default pdfReport;
