import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Form, Button, Row, Col, Radio, Select } from 'antd';
import { Field, reduxForm } from 'redux-form';
import moment from 'moment';
import styleAntd from '../../../../infrastructure/shared/styleAntd';
import ui from '../../../../application/selectors/ui';
import { getAllLaporanStockGlobalProduksi } from '../../../../application/actions/laporanstockglobalproduksi';
import { setTableLayout } from '../../../../application/actions/ui';

const { Option } = Select;
const dateFormat = 'DD/MM/YYYY';
const today = new Date();
const initialValues = {
  tanggal: moment(today, dateFormat),
  user: 'all',
  tipe_laporan: 'L',
  divisi: false,
  repair: false,
};
const FormLaporanGlobalProduksi = (prop) => {
  const btnLoading = useSelector(ui.getBtnLoading);
  const dispatch = useDispatch();
  return (
    <Form layout="vertical">
      <Row>
        <Col>
          <Field
            name="date"
            type="date"
            label="Tanggal"
            component={styleAntd.ADatePick}
            className="form-item-group"
            onBlur={(e) => e.preventDefault()}
            defaultValue={moment(today, dateFormat)}
          />
        </Col>
        <Col offset={1}>
          <Field
            name="user"
            label="User"
            style={{ width: 250 }}
            component={styleAntd.ASelect}
            placeholder="Pilih User"
            defaultValue="all"
            onBlur={(e) => e.preventDefault()}
          >
            <Option value="all">Semua</Option>
            <Option value="admin1">Admin 1</Option>
            <Option value="admin2">Admin 2</Option>
          </Field>
        </Col>
        <Col offset={1}>
          <Field
            name="tipe_laporan"
            label="Tipe Laporan"
            defaultValue="L"
            component={styleAntd.ARadioGroup}
          >
            <Radio value="L" onClick={() => dispatch(setTableLayout('L'))}>
              L
            </Radio>
            <Radio value="R" onClick={() => dispatch(setTableLayout('R'))}>
              R
            </Radio>
          </Field>
        </Col>
        <Col offset={1}>
          <Field
            label="Semua Divisi"
            name="divisi"
            id="divisi"
            component={styleAntd.ACheckBox}
            type="checkbox"
          />
        </Col>
        <Col offset={1}>
          <Field
            label="Tanpa Reparasi"
            name="repair"
            id="repair"
            component={styleAntd.ACheckBox}
            type="checkbox"
          />
        </Col>
      </Row>
      <Row>
        <Col offset={21}>
          <Button
            type="primary"
            htmltype="button"
            loading={btnLoading}
            onClick={() => prop.dispatch(getAllLaporanStockGlobalProduksi)}
          >
            Lihat Laporan
          </Button>
        </Col>
      </Row>
    </Form>
  );
};

export default reduxForm({
  form: 'FormLaporanGlobalProduksi',
  initialValues: {
    date: moment(today, dateFormat),
    user: 'all',
    tipe_laporan: 'L',
    divisi: false,
    repair: false,
  },
})(FormLaporanGlobalProduksi);
