import { jsPDF } from 'jspdf';
import 'jspdf-autotable';

const pdfReportR = (data = '') => {
  const doc = new jsPDF('l', 'mm', [420, 210]);
  let tableRows = [];
  let tableColumn = [];

  const tanggal = new Date(localStorage.getItem('tanggal_lap'));
  let finalY = 30;
  doc.text('Laporan Stock Global Produksi R', 14, 15);
  doc.setFontSize(20);
  doc.text('Nama Toko', 200, 15);

  doc.setFontSize(10);
  doc.setProperties({
    title: 'Bahan Tukang Detail',
  });
  doc.text(`PERIODE : ${tanggal.toLocaleDateString()}`, 14, 25);

  tableColumn = [
    [
      {
        content: `DIVISI`,
        rowSpan: 2,
      },
      {
        content: `EAY18`,
        colSpan: 4,
      },
      {
        content: `EAY14`,
        colSpan: 4,
      },
      {
        content: `EAY10`,
        colSpan: 4,
      },
      {
        content: `EAW18`,
        colSpan: 4,
      },
      {
        content: `EAW14`,
        colSpan: 4,
      },
      {
        content: `EAW10`,
        colSpan: 4,
      },
      {
        content: `TOTAL`,
        rowSpan: 2,
      },
    ],
    [
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
      {
        content: `AWAL`,
      },
      {
        content: `MASUK`,
      },
      {
        content: `KELUAR`,
      },
      {
        content: `SALDO`,
      },
    ],
  ];

  let total = 0;
  data.forEach((element) => {
    total =
      element.eay18_awal +
      element.eay18_in +
      element.eay18_out +
      element.eay18_sld +
      element.eay14_awal +
      element.eay14_in +
      element.eay14_out +
      element.eay14_sld +
      element.eay10_awal +
      element.eay10_in +
      element.eay10_out +
      element.eay10_sld +
      element.eaw18_awal +
      element.eaw18_in +
      element.eaw18_out +
      element.eaw18_sld +
      element.eaw14_awal +
      element.eaw14_in +
      element.eaw14_out +
      element.eaw14_sld +
      element.eaw10_awal +
      element.eaw10_in +
      element.eaw10_out +
      element.eaw10_sld;
    const row = [
      {
        content: element.divisi,
        styles: {
          halign: 'left',
        },
      },
      {
        content: element.eay18_awal.toFixed(3),
      },
      {
        content: element.eay18_in.toFixed(3),
      },
      {
        content: element.eay18_out.toFixed(3),
      },
      {
        content: element.eay18_sld.toFixed(3),
      },
      {
        content: element.eay14_awal.toFixed(3),
      },
      {
        content: element.eay14_in.toFixed(3),
      },
      {
        content: element.eay14_out.toFixed(3),
      },
      {
        content: element.eay14_sld.toFixed(3),
      },
      {
        content: element.eay10_awal.toFixed(3),
      },
      {
        content: element.eay10_in.toFixed(3),
      },
      {
        content: element.eay10_out.toFixed(3),
      },
      {
        content: element.eay10_sld.toFixed(3),
      },
      {
        content: element.eaw18_awal.toFixed(3),
      },
      {
        content: element.eaw18_in.toFixed(3),
      },
      {
        content: element.eaw18_out.toFixed(3),
      },
      {
        content: element.eaw18_sld.toFixed(3),
      },
      {
        content: element.eaw14_awal.toFixed(3),
      },
      {
        content: element.eaw14_in.toFixed(3),
      },
      {
        content: element.eaw14_out.toFixed(3),
      },
      {
        content: element.eaw14_sld.toFixed(3),
      },
      {
        content: element.eaw10_awal.toFixed(3),
      },
      {
        content: element.eaw10_in.toFixed(3),
      },
      {
        content: element.eaw10_out.toFixed(3),
      },
      {
        content: element.eaw10_sld.toFixed(3),
      },
      {
        content: total.toFixed(3),
      },
    ];
    tableRows.push(row);
  });

  const footer = [
    {
      content: '',
    },
    {
      content: data.reduce((a, b) => a + b.eay18_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay18_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay18_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay18_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay14_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay14_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay14_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay14_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay10_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay10_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay10_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eay10_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw18_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw18_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw18_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw18_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw14_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw14_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw14_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw14_sld, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw10_awal, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw10_in, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw10_out, 0).toFixed(3),
    },
    {
      content: data.reduce((a, b) => a + b.eaw10_sld, 0).toFixed(3),
    },
    {
      content: '',
    },
  ];
  tableRows.push(footer);

  const printed = [
    {
      content: `Printed By Admin`,
      colSpan: 12,
      styles: {
        fontStyle: 'italic',
        textColor: '#000',
        halign: 'left',
      },
    },
  ];
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: finalY,
    theme: 'plain',
    margin: { top: 10 },
    bodyStyles: {
      fontSize: 8,
      halign: 'right',
    },
    headStyles: {
      fontSize: 8,
      fillColor: '#E8E5E5',
      textColor: '#000',
      valign: 'middle',
      halign: 'center',
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  tableRows = [];
  tableColumn = [];
  finalY = doc.autoTableEndPosY() + 20;

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width;
  const pageHeight = doc.internal.pageSize.height;
  doc.setFontSize(10);
  for (let j = 1; j < pages + 1; j += 1) {
    const horizontalPos = pageWidth / 2;
    const verticalPos = pageHeight - 10;
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: 'center',
    });
  }
  const string = doc.output('datauristring');
  const x = window.open();
  x.document.open();
  x.document.write(
    `<html>
    <head>
    <title>Bahan Tukang Detail</title>
    </head>
    <body style='margin:0 !important'>
    <embed width='100%' height='100%'src='${string}'></embed>
    </body>
    </html>`,
  );
};

export default pdfReportR;
